body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: #f9f7f7;
}

.headerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 130px;
  background-color: blue;
  background-image: -webkit-linear-gradient(left, #1d5994 ,#24955f);
  background-image: linear-gradient(to right, #1d5994 ,#24955f);
  color: #fff;
  font-size: 38px;
  font-weight: bold;
  text-align: center;
}

.bodyContainer {
  background-color: #fff;
  max-width: 900px;
  margin: auto;
  padding: 10px;
  border-left: 1px solid #c0c0c0;
  border-right: 1px solid #c0c0c0;
  border-bottom: 1px solid #c0c0c0;
}

.searchBarContainer {
  display: flex;
  padding: 15px;
  flex: 1 1;
}

.searchBarInput {
  flex: 1 1;
  height: 40px;
  border: 1px solid #c0c0c0;
  padding-left: 10px;
  font-size: 17px;
  border-radius: 2px;
}

.searchBarInput:focus {
  outline: none;
}

.searchBarButton {
  height: 44px;
  width: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #24995c;
  color: #fff;
  margin-left: 15px;
  border-radius: 2px;
  box-shadow: 1px 1px 1px #888888;
}

.searchBarButtonIcon {
  color: red;
  -webkit-transform: rotate(-10 50 100);
          transform: rotate(-10 50 100);
}

.resultsContainer {
  display: flex;
  flex-direction: column;
  padding: 0 15px 15px;
}

ul {
  padding-left: 19px;
  margin: 0;
}

ul li { 
  padding: 0 0 10px; 
}

.textContainer {
  text-align: center;
  font-size: 19px;
  font-weight: 500;
}

.favouritesTitle {
  font-size: 28px;
  color: #24995c;
  font-weight: 700;
  margin-bottom: 20px;
}

.favouritesHint {
  font-size: 19px;
  font-weight: 400;
}
/* styling for <Result /> component */

.resultContainer {
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
}

.resultStar {
  margin-right: 12px;
}

.resultTitle {
  flex: 1 1;
  margin: 0;
}

.resultBody {
  flex: 1 1;
}
